import axios from "axios";
import * as actions from "../../redux";


import { FETCH_APPS } from './projectTypes';



export const fetchMyApps = () => async dispatch => {
    const res = await axios.get("https://mareesdroid-github-backend.herokuapp.com/data");
   
    console.log(res.data);
    dispatch({ type: FETCH_APPS, payload: res.data });
  };
  
export const deleteMyApp = (idHere,type) => async dispatch => {
  dispatch(actions.transactionRequest())
  try{
    const res = await axios.delete(`https://mareesdroid-github-backend.herokuapp.com/data/${idHere}`, { params: {id: idHere}});
    if(res.data.info === 'Successfully Deleted..'){
      dispatch(actions.transactionSuccess())
      dispatch(actions.fetchMyApps())
    //  return dispatch( { type: DELETE_APP, payload: idHere });
    }else{
      dispatch(actions.transactionFailed())
    }
   
  } catch(error){
    dispatch(actions.transactionFailed())
  }
}


export const updateMyApp = (app) => async dispatch => {
  /**
   * dont send data exactly _id is auto generated u need to change the key name to id dont use _id
   * otherwiese it throws error;
   *  Performing an update on the path '_id' would modify the immutable field '_id'
   */

   /** Update in backend if u add _id as id this error will not show
    * below method changes the key but always creates new obj in db not updating
    
  // for rename keyname
  let origKey = '_id';
  let newKey = 'id';

  console.log(app);
  Object.defineProperty(app, newKey,
  Object.getOwnPropertyDescriptor(app, origKey));
  delete app[origKey];

  console.log(app);
  */
  dispatch(actions.transactionRequest())
  try{

    const res = await axios.put(`https://mareesdroid-github-backend.herokuapp.com/data/`, app);
    if(res.data.info === 'success'){
      dispatch(actions.transactionSuccess())
      dispatch(actions.fetchMyApps())
      //return dispatch( { type: UPDATE_APP });
    }else if(res.data.info === 'failed'){
      dispatch(actions.transactionFailed())
        console.log(res.data.msg);
    } else{
      dispatch(actions.transactionFailed())
    }
   
  } catch(error){
    dispatch(actions.transactionFailed())
  }
}

