import React,{useEffect,useState, useRef} from "react";
import icon from "../../download.png";
import "../../note.css";
import Modal from "./Modal";


const AppView = (props)=>{
  const [myJsx, setMyJsx] = useState(<div></div>);
  
    return(
      <div>
       
        <div class="container-fluid"  >
         
<div class="fake-phone-window " >
 {myJsx}
  <div class="row" >
    <div  >
     <img src={icon} style={{height:"128px", width:"128px"}}/>
    </div>
    <div >
  <h3 >{props.numberOfApps[props.index].name}</h3>

  
  <p >{props.numberOfApps[props.index].description}</p>  
    </div>
  </div>
  </div>
  <div>
  
  
  {renderActions(props,setMyJsx ,myJsx)}
   
 
 
  
 
  </div>
  <div   class="fake-droid-nav">
  <i class="material-icons" onClick={()=>props.handle("app list")} style={{color:"white", float:"left"}}>arrow_back</i>
  <i class="material-icons" onClick={()=>props.handle("home")} style={{color:"white", float:"center"}}>home</i>

  </div>
  </div>
  </div>
    );
};

const renderActions = (props ,setMyJsx, myJsx)=>{
  let isAdmin = true;



  const renderUpdate = (data)=>{
    return setMyJsx(<Modal data={data} action="update"/>);
  };

  const renderDelete = (data)=>{
    return setMyJsx(<Modal data={data} action="delete" />);
  };
  if(isAdmin)
               return(  
               <div class="row app-action">
               <div class="col s6 m6 l6">
                  <a onClick={()=>renderUpdate(props.numberOfApps[props.index])}   class="col s10 m10 l10 amber lighten-5 grey-text text-darken-3 waves-effect waves-light btn-small">Update</a>
                  </div>
                  <div class=" col s6 m6 l6">
                  <a onClick={()=>renderDelete(props.numberOfApps[props.index])} class="col s10 m10 l10 amber lighten-5 grey-text text-darken-3 waves-effect waves-light btn-small">Delete</a>
                  </div>
                </div>
               );
 
    
    return(
      <div class="row app-action">
      <div class="col s6 m6 l6">
      <a class="col s10 m10 l10 amber lighten-5 grey-text text-darken-3 waves-effect waves-light btn-small"><i class="material-icons right">ondemand_video</i>Demo</a>
      </div>
      <div class=" col s6 m6 l6">
      <a class="col s10 m10 l10 amber lighten-5 grey-text text-darken-3 waves-effect waves-light btn-small"><i class="material-icons right">file_download
  </i>Apk </a>
      </div>
      </div>
    );
}


export default AppView;