import React, {useState, useEffect} from "react";
import AppList from "./AppList";
import AppView from "./AppView";
import PhoneHome from "./PhoneHome";
import { connect } from "react-redux";


const Android = (props)=>{


  let androidApps = [];

const getApps = ()=>{
  if(props.apps)
   return props.apps.android;

   return [];
}

androidApps = getApps();

 

    const [mode, setMode] = useState("home");
    const [index, setIndex] = useState(0);
    const [chosen, setChosen] = useState("Apps");
    
    
    
  
      const handleChang = (mode)=>{
        setMode(mode);
      }

      const assignView = ()=>{
        if(mode === "app list")
        {
          return (
            <div>
            <h1>{chosen}</h1>
          <AppList numberOfApps={androidApps} handle = {handleChang} setIndex = {setIndex}/>
          </div>
          );
        } else if(mode ==="app view"){
          return <AppView numberOfApps={androidApps} handle = {handleChang} index = {index}/>
        } else{
          return <PhoneHome handle = {handleChang} choose={setChosen}/>
        }
           
   
       
      }

console.log("test");

const showHome = ()=>{
  console.log("test");
  setMode("home");
  setIndex(0);
  setChosen("Apps");
  assignView();

};

    return(
      <div>
      <h1 style={{textAlign:"center"}}>Android</h1>
<div class="smartphone">
  
 <div class="my-container">

     <div class="content myRow">
    
          {assignView()}

          <div class="fake-home" onClick={()=>showHome()}>

          </div>

  </div>
  </div>
  </div>
</div>


       
       
  
    );
}
const mapStateToProps = (state)=>{

  return{
    apps: state.project
  }

};

export default connect(mapStateToProps, null)(Android);