import {TRANSACTION_REQUEST, TRANSACTION_SUCCESS, TRANSACTION_FAILURE, RESET, ADD_REQUEST, ADD_SUCCESS, ADD_FAILURE, UPDATE_REQUEST, UPDATE_SUCCESS, UPDATE_FAILURE } from './listenerType';
const initialState = {
    loading: false,
    resp: [],
    error: ''
}

const listenerReducer = (state = initialState, action) => {
    switch(action.type){
        case TRANSACTION_REQUEST:
            return{
                ...state,
                loading: true
            }
        
        case TRANSACTION_SUCCESS:
            return{
                loading: false,
                resp: 'Success',
                error: ''
            }
        case TRANSACTION_FAILURE:
            return{
                loading: false,
                resp: 'failed',
                error: action.payload
            }
        case RESET:
            return{
                ...state,
                resp: []
            }
        default: 
            return state;
    }
}

export default listenerReducer;