import React,{useRef, useEffect, useState} from "react";
import * as actions from "../../redux";
import { connect } from "react-redux";
import M from "materialize-css";
const ModalFrame = (props) => {

  const [mode, setMode] = useState('');
  const handleDelete = (data)=>{
    setMode('delete');
    props.deleteMyApp(data._id,data.type);
  }

  const handleUpdate = (data)=>{
  
    let name = document.getElementById('name').value.trim();
    let logo = document.getElementById('logo').value.trim();
    console.log(data);
    if(name !== '' || logo !== ''){
      if(name === '')
        name = data.name;
      if(logo === '')
        logo = data.logo;

    data.name = name;
    data.logo = logo;
    setMode('update');
    props.updateMyApp(data);
    return;
    }
    M.toast({html: 'Nothing to Update!'});
  }

  const mounted = useRef();
useEffect(() => {
  if (!mounted.current) {
    mounted.current = true;
  } else {
    // do componentDidUpate logic
    if(props.response){
      if(props.response.resp === 'Success'){
        switch(mode){
          case 'update':
             M.toast({html: 'Updated Successfully!'});
            break;
          case 'delete':
            M.toast({html: 'Deleted Successfully!'});
            break;
        }
       
       
        props.reset();
      }
      else if(props.response.resp === 'failed'){
        M.toast({html: 'Something Went Wrong!'});
      }
     }
    
  }
});


    const renderForm = ()=>{

        return(
          <div class="row">
        <form class="col s12">
          <div class="row col s12 m12 l12">
          <div class="input-field col s12 m12 l12">
          <i class="material-icons prefix">account_circle</i>
                <input id="name" type="text" data-length="15" />
        <label for="name">{props.data.name}</label>
                <span class="helper-text" data-error="Only 15 chars are allowed"></span>
              </div>
       
           
          </div>
         

          <div class="row col s12 m12 l12">
          <div class="input-field col s12 m12 l12">
          <i class="material-icons prefix">account_circle</i>
                <input id="logo" type="text"  />
        <label for="logo">Logo Url</label>
                
              </div>
       
           
          </div>
         
       
      
        </form>
      </div>
        );
    
      };

      
   
    return(
   <div>
    <div className="modal-content">
<h4>{props.data.type}</h4>

{renderForm()}


     
    </div>
    <div class="modal-footer">
      <a class="modal-close waves-effect waves-red btn-flat">
        Cancel
      </a>
      <a onClick = {()=>handleUpdate(props.data)} class="modal-close modal-close waves-effect waves-green btn-flat">
        Update
      </a>
      <div onClick={()=>handleDelete(props.data)} class=" modal-close waves-effect waves-red btn-flat">
        Delete
      </div>
    </div>
    </div>
);

}

const mapStateToProps = (state)=>{
  console.log(state)
  return{
    response: state.response,
    project: state.project
  }
}

export default connect(mapStateToProps, actions)(ModalFrame);