import React, { useRef, useEffect } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";

import Update from "./Update";
import Delete from "./Delete";


const Modal = (props)=>{


    let modalRef = useRef();

    useEffect(() => {

        document.addEventListener("DOMContentLoaded", function () {
            var textNeedCount = document.querySelectorAll("#name, #message");
            M.CharacterCounter.init(textNeedCount);
      });
          const options = {
            onOpenStart: () => {
              console.log("Open Start");
            },
            onOpenEnd: () => {
              console.log("Open End");
            },
            onCloseStart: () => {
              console.log("Close Start");
            },
            onCloseEnd: () => {
              console.log("Close End");
            },
            inDuration: 250,
            outDuration: 250,
            opacity: 0.5,
            dismissible: false,
            startingTop: "4%",
            endingTop: "10%"
          };
          console.log(modalRef);
          let instance = M.Modal.init(modalRef.current, options);
         instance.open();
          // If you want to work on instance of the Modal then you can use the below code snippet 
          // let instance = M.Modal.getInstance(this.Modal);
          // instance.open();
          // instance.close();
          // instance.destroy();
      
      
      });


      const renderContent = ()=>{
            console.log("test")
        switch(props.action){

            case "delete":
                    return <Delete data={props.data}/>;
            default:
                return <Update data={props.data}/>;

        }
        
      };



  
 
  
      
    return (
        <div
        ref={modalRef}
        id="modal1"
        className="modal"
      >
      { renderContent() }
      </div>
    );

}

export default Modal;