import React,{useEffect} from "react";
import "../style.css";
import "../logo.scss";
import $ from "jquery";
import { Link } from "react-router-dom";




function Header(props){
	

	useEffect(() => {
		if($){

			var width = 100,
			perfData = window.performance.timing, 
			EstimatedTime = -(perfData.loadEventEnd - perfData.navigationStart),
			time = ((EstimatedTime/1000)%50) * 5
	
	
		// Percentage Increment Animation
		var PercentageID = $(".percentage"),
				start = 0,
				end = 100,
				durataion = time;
				animateValue(PercentageID, start, end, durataion);
	
		function animateValue(id, start, end, duration) {
	
			var range = end - start,
			  current = start,
			  increment = end > start? 1 : -1,
			//   stepTime = Math.abs(Math.floor(duration / range)),
			  obj = $(id);
	
	
			var timer = setInterval(function() {
				current += increment;
				$(obj).text(current);
			  //obj.innerHTML = current;
				if (current == end) {
					clearInterval(timer);
				}
			}, 5);
		}
		
	
		
	
		$(".preloader").fadeOut();
			
			$(".cd-transition-layer").addClass("closing").delay(0).queue(function(){
				$(this).removeClass("visible closing opening").dequeue();
			});	
		
	
	
	   // FADE OUT EFFECT WHEN CLICK A LINK
		$(document).on("click", "a:not(.lightbox)", function () {
			var newUrl = $(this).attr("href");
			if (!newUrl || newUrl[0] === "#") {
				window.location.hash = newUrl;
				return;
			}
			$("html").fadeOut(function () {
				window.location = newUrl;
			});
			return false;
		});
		
	
		var paget = $(".page-title .title").text();
	
		$( ".page-title").append("<span></span>");
		$( ".page-title span").append(paget);
	
	
		
		//posts page hover 
		$(".blog-post .blog-link").hover(function(){
			$(this).parent(".content-outter").parent(".blog-post").toggleClass("mousef");
			$(this).parent(".blog-post").toggleClass("mousef");
		});
	 
	 
	
	
	
	
	$(window).on("load", function() {
	 
	
	function smokeeffect () { 
		var modalTrigger = $(".halfway-fab"),
			transitionLayer = $(".cd-transition-layer"),
			transitionBackground = transitionLayer.children(),
			modalWindow = $(".full-menu");
	
		var frameProportion = 1.78, //png frame aspect ratio
			frames = 25, //number of png frames
			resize = false;
	
		//set transitionBackground dimentions
		setLayerDimensions();
		$(window).on("resize", function(){
			if( !resize ) {
				resize = true;
				(!window.requestAnimationFrame) ? setTimeout(setLayerDimensions, 300) : window.requestAnimationFrame(setLayerDimensions);
			}
		});
	
		//open modal window
		modalTrigger.on("click", function(event){   
			event.preventDefault();
			transitionLayer.addClass("visible opening");
			var delay = ( $(".no-cssanimations").length > 0 ) ? 0 : 600;
			setTimeout(function(){
				modalWindow.addClass("visible");
			}, delay);
		});
	
		//close modal window
		modalWindow.on("click", ".modal-close", function(event){
			event.preventDefault();
			transitionLayer.addClass("closing");
			modalWindow.removeClass("visible");
			transitionBackground.one("webkitAnimationEnd oanimationend msAnimationEnd animationend", function(){
				transitionLayer.removeClass("closing opening visible");
				transitionBackground.off("webkitAnimationEnd oanimationend msAnimationEnd animationend");
			});
		});
	
		function setLayerDimensions() {
			var windowWidth = $(window).width(),
				windowHeight = $(window).height(),
				layerHeight, layerWidth;
	
			if( windowWidth/windowHeight > frameProportion ) {
				layerWidth = windowWidth;
				layerHeight = layerWidth/frameProportion;
			} else {
				layerHeight = windowHeight*1.2;
				layerWidth = layerHeight*frameProportion;
			}
	
			transitionBackground.css({
				"width": layerWidth*frames+"px",
				"height": layerHeight+"px",
			});
	
			resize = false;
		}
	
	}
	smokeeffect()
	
	});
		
		}
	  }, []);

   
    return(
<div>
<link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet" />

		{/* // navbar */}
		<nav style = {{backgroundColor: "wheat"}}class="nav-extended">
		<div class="nav-wrapper">
		 {/* logo */}

<div class="stage" style={{width:"20%", paddingTop: "20px"}}>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
  <div class="layer"></div>
</div>
	
		  <ul class="right hide-on-med-and-down">
			<li> <Link class="grey-text text-darken-3" to="/works">My Works</Link></li>
			<li><Link class="grey-text text-darken-3" to="/about">About</Link></li>
			<li><Link class="grey-text text-darken-3" to="/">Home</Link></li>
		  </ul>
		</div>
		<div class="nav-content"style={{padding:"20px"}}>
		 
		  <a class="btn-floating btn-large halfway-fab waves-effect waves-light"style={{backgroundColor:"#282c34"}}>
			<i class="material-icons" style={{color:"gold"}}>menu</i>
		  </a>
		</div>
	  </nav>


{/* after-smoke */}
<div class="full-menu">
	<div class="fullmenu-content">
		<ul class="page-menu">
			<Link to="/">Home</Link>
			<Link to="/contact">Contact</Link>
			<Link to="/resume">Resume</Link>
			
			
		</ul>
	</div>
	<div className="modal-close">
		
<i class="material-icons md-48">close</i>
		</div>
	
</div>
<div class="cd-transition-layer visible opening"> 
	<div class="bg-layer"></div>
</div>


</div>



    );
}


export default Header;