import {TRANSACTION_REQUEST, TRANSACTION_SUCCESS, TRANSACTION_FAILURE, RESET, ADD_REQUEST, ADD_SUCCESS, ADD_FAILURE, UPDATE_REQUEST, UPDATE_SUCCESS, UPDATE_FAILURE } from './listenerType';


export const transactionRequest = () => {
    return{
        type: TRANSACTION_REQUEST
    }
};

export const transactionSuccess = (users) => {
    return{
        type: TRANSACTION_SUCCESS,
        payload: users
    }
};

export const transactionFailed = (error) => {
    return{
        type: TRANSACTION_FAILURE,
        payload: error
    }
};

export const reset = ()=>{
    return{
        type: RESET
    }
}
