import React, { Component } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import $ from "jquery";

class Modal extends Component {
  componentDidMount() {


   
    document.addEventListener("DOMContentLoaded", function () {
      var textNeedCount = document.querySelectorAll("#name, #message");
      M.CharacterCounter.init(textNeedCount);
});
    const options = {
      onOpenStart: () => {
        console.log("Open Start");
      },
      onOpenEnd: () => {
        console.log("Open End");
      },
      onCloseStart: () => {
        console.log("Close Start");
      },
      onCloseEnd: () => {
        console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%"
    };
    M.Modal.init(this.Modal, options);
    // If you want to work on instance of the Modal then you can use the below code snippet 
    // let instance = M.Modal.getInstance(this.Modal);
    // instance.open();
    // instance.close();
    // instance.destroy();
  }

  renderForm = ()=>{

    return(
      <div class="row">
    <form class="col s12">
      <div class="row col s12 m12 l12">
      <div class="input-field col s12 m12 l12">
      <i class="material-icons prefix">account_circle</i>
            <input id="name" type="text" data-length="20" />
            <label for="name">Input text</label>
            <span class="helper-text" data-error="Only 20 chars are allowed"></span>
          </div>
     
       
      </div>
     
     
      <div class="row col s12 m12 l12">
        <div class="input-field col s12 m12 l12">
        <i class="material-icons prefix">email</i>
          <input id="email" type="email" class="validate" />
          <label for="email">Email</label>
          <span class="helper-text" data-error="wrong"></span>
        </div>
      </div>

     
   
    <div class="row col s12 m12 l12">
          <div class="input-field col s12 m12 l12">
          <i class="material-icons prefix">mode_edit</i>
            <textarea id="message" class="materialize-textarea" data-length="1200"></textarea>
            <label for="message">Textarea</label>
            <span class="helper-text" data-error="Only 20 chars are allowed"></span>
          </div>
        </div>
   
    </form>
  </div>
    );

  };

  render() {
    return (
      <>
        <a
          className="waves-effect waves-light btn modal-trigger"
          data-target="modal1"
        >
          Modal
        </a>

        <div
          ref={Modal => {
            this.Modal = Modal;
          }}
          id="modal1"
          className="modal"
        >
          {/* If you want Bottom Sheet Modal then add 
        bottom-sheet class */}
          <div className="modal-content">
            <h4>Contact</h4>
            { this.renderForm() }
          </div>
          <div class="modal-footer">
            <a href="#" class="modal-close waves-effect waves-red btn-flat">
              Cancel
            </a>
            <a href="#" class="modal-close waves-effect waves-green btn-flat">
              Send
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default Modal;