import { FETCH_APPS } from './projectTypes';


const sortData = (myProjects)=>{

  let languages = [];
  let frameworks = [];
  let build = [];
  let database = [];
  let ide = [];
  let web = [];
  let android = [];

  myProjects.forEach(project=>{
    switch (project.type){
      case "language":
          languages.push(project);
        break;
      case "framework":
          frameworks.push(project);
        break;
      case "data":
        database.push(project);
        break;
      case "ide":
          ide.push(project);
        break;
      case "build":
          build.push(project);
        break;
      case "web":
          web.push(project);
        break;
      case "android":
          android.push(project);
        break;
        
    }
  });

  return{
    language: languages,
    framework: frameworks,
    data: database,
    ide: ide,
    build: build,
    web: web,
    android: android
  }

};

export default function(state = null, action) {
  console.log(action.type)
  switch (action.type) {
    case FETCH_APPS:
      console.log(action.payload);
      if(action.payload){
        return sortData(action.payload);
      }       
    default:
      return state;
  }
}
