import React,{useEffect} from "react";


import "../../note.css";



const AppList = (props) => {
  
  
    let appContent = [];

    const chosenApp = (index)=>{
      props.handle("app view")
      props.setIndex(index)
    }
    
    const androidContent = (data,index) => {
      return(
       
       
        <div class="test inner" onClick={()=>chosenApp(index)}>
          
          <div class="col s6 m6 l6 card app-card  #cfd8dc blue-grey lighten-4">
            <div class="card-content white-text" style={{alignContent: "center", textAlign: "center"}}>
           
            <img src={data.logo} style={{height:"98px", width:"98px"}}/>
            <p style = {{width:"100%", textAlign:"center", fontSize:"13px", color: "black"}}>{data.name}</p>
            </div>
           
          </div>
        </div>
      
     
          );
        };
        
      props.numberOfApps.forEach((data,index)=>{
            appContent.push(androidContent(data,index));
        });
   
       

  return appContent;
}

export default AppList;